import axios from '@/libs/api.request'

export const getEmpList = (data) => {
  return axios.request({
    url: '/activity/emp/list',
    method: 'get',
    params: data
  })
}

export const queryEmp = (data) => {
  return axios.request({
    url: '/activity/emp/query',
    method: 'get',
    params: data
  })
}
