<template>
  <div class="search-container">
    <div class="search-wrapper">
      <div class="search-content">
        <img src="../assets/search.png">
        <input type="search" placeholder="直接搜索选手姓名进行投票" @keyup.enter="queryEmpByName" v-model="search">
      </div>
      <div class="btn-search" @click="queryEmpByName">
        <span>搜索</span>
      </div>
    </div>
    <div class="query-list">
      <div class="list-item" v-for="(item, index) in queryEmpData" :key="index">
        <span class="index">{{index+1}}</span>
        <span class="text">{{item.empEntity.departmentEntity.name}}-{{item.empEntity.empName}}</span>
        <span class="no">{{item.number}}</span>
        <span class="vote" @click="userVoteEmpTicket(item)">投票</span>
      </div>
    </div>
    <mt-popup v-model="votePopupVisible" popup-transition="popup-fade" closeOnClickModal="false">
      <div class="prompt-form">
        <img class="close" src="../assets/close.png" @click="closePopup">
        <div class="top">
          <img src="../assets/voteHand.png" />
        </div>
        <div class="content">
          <span class="red" @click="reduce">-</span>
          <input type="number" v-model="myVoteTicket" placeholder="请输入投票数量">
          <span class="red" @click="add">+</span>
        </div>
        <div class="tip">
          当前剩余可投票数: {{ticketNumber}}
        </div>
        <div class="btn-wrapper">
          <span class="btn-vote" @click="userVoteEmpBtn">
            确认投票
          </span>
        </div>
      </div>
    </mt-popup>
    <div v-if="queryEmpData && queryEmpData.length==0" class="no-data">
      <p>查询无结果</p>
    </div>
  </div>
</template>

<script>

import { getEmpList, queryEmp } from '@/api/employee'
import { getTicketNumber, userVoteEmp } from '@/api/ticket'
import jsAPI from '../libs/jsAPI'
import { Toast, MessageBox, Indicator, Popup } from 'mint-ui';
import { login, getUserInfo } from '@/api/user'

export default {
  name: 'SearchView',
  components: {

  },
  data() {
    return {
      votePopupVisible: false,
      search: '',
      myVoteTicket: 0,
      empEntity: {},
      queryEmpData: null
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user;
    },
    ticketNumber() {
      return this.$store.state.ticketNumber
    }
  },
  created() {

  },
  methods: {
    add: function () {
      if (this.myVoteTicket == '' || this.myVoteTicket == ' ') {
        this.myVoteTicket = 0;
      }
      this.myVoteTicket = parseInt(this.myVoteTicket);
      this.myVoteTicket += 1;
    },
    reduce: function () {
      if (this.myVoteTicket <= 0) {
        return;
      }
      this.myVoteTicket = parseInt(this.myVoteTicket);
      this.myVoteTicket -= 1;
    },
    getTicketNumber() {
      getTicketNumber().then(res => {
        this.$store.commit({
          type: 'setTicketNumber',
          ticketNumber: res.data.result.number
        });
      });
    },
    userVoteEmpBtn() {
      console.log('userVoteEmpBtn');
      console.log(this.empEntity);
      if (this.myVoteTicket < 1) {
        Toast({
          message: '1票起投',
          position: 'middle',
          duration: 2000
        });
        return;
      }
      if (this.myVoteTicket > this.ticketNumber) {
        Toast({
          message: '超过最大票数',
          position: 'middle',
          duration: 2000
        });
        return;
      }
      this.userVoteEmp(this.empEntity.empId, this.myVoteTicket);
      this.votePopupVisible = false;
    },
    queryEmpByName() {
      console.log(this.search);
      if (this.search == '' || this.search == ' ') {
        Toast({
          message: '不能为空',
          position: 'middle',
          duration: 2000
        });
        return;
      }
      let params = {
        name: this.search
      }
      queryEmp(params).then(res => {
        if (res.data.resCode == 0) {
          //成功请求数据
          this.queryEmpData = res.data.result;
          console.log(this.queryEmpData);
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
    hasLogin: function () {
      if (this.userInfo.clientId.length > 1) {
        return true;
      }
      return false;
    },
    userVoteEmp: function (empId, ticketNumber) {
      let params = {
        empId,
        ticketNumber
      }
      userVoteEmp(params).then(res => {
        if (res.data.resCode == 0) {
          //成功请求数据
          this.queryEmpByName();
          this.getTicketNumber();
          Toast({
            message: '投票成功',
            position: 'middle',
            duration: 2000
          });
        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    },
    userVoteEmpTicket(data) {
      this.empEntity = data.empEntity;
      if (this.hasLogin()) {
        this.votePopupVisible = true;
      } else {
        this.forwardLogin();
      }
    },
    forwardLogin: function () {
      let that = this;
      window.loginCallBack = function () {
        // 登陆回来从新初始化信息
        console.log('loginCallBack');
        that.$store.dispatch('userLogin');
      }
      jsAPI.forwardLogin('loginCallBack');
    },
    closePopup: function () {
      this.votePopupVisible = false;
    }
  }
}
</script>
